import React, { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import rehypeSanitize from "rehype-sanitize"; // ✅ HTML 태그를 안전하게 렌더링하는 플러그인

interface MarkdownRendererProps {
  filePath: string; // Markdown 파일 경로
}

const MarkdownRenderer: React.FC<MarkdownRendererProps> = ({ filePath }) => {
  const [content, setContent] = useState<string>("");

  useEffect(() => {
    const loadMarkdown = async () => {
      try {
        const response = await fetch(filePath);
        if (!response.ok) {
          throw new Error(`Failed to load Markdown file: ${filePath}`);
        }
        const text = await response.text();
        setContent(text);
      } catch (error) {
        console.error("Error fetching Markdown file:", error);
      }
    };

    loadMarkdown();
  }, [filePath]);

  return (
    <div className="w-full max-w-[900px] mx-auto prose">
      {content ? (
        <ReactMarkdown
          remarkPlugins={[remarkGfm]} // ✅ GitHub 스타일 Markdown 지원
          rehypePlugins={[rehypeSanitize]} // ✅ HTML 태그를 안전하게 렌더링
        >
          {content}
        </ReactMarkdown>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
};

export default MarkdownRenderer;
